.home{
    max-width: 1500px;
    margin: 0 auto;
    
}
.home__img{
    width: 100%;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
    z-index: -1;
    margin-bottom: -250px;
}
.home__row{
    display: flex;
    z-index: 1;
    max-width: 1500px;
    margin: 0 auto;
}
@media  (max-width: 600px) {
    .home__img{
        margin-bottom: -80px;
    }
}