.subtotal{
    display: flex;
    flex-direction: column;
    width: 250px;
    height: 100px;
    padding: 20px;
    border: 1px solid #dddddd ;
    border-radius: 3px;
    justify-content: space-between;
    margin-right: 20px;
    
}
.subtotal__btn{
    cursor: pointer;
    width: fit-content;
    padding: 5px;    
    background-color: #FFD814;
    border: none;
    
    padding-left: 28px;
    padding-right: 28px;
    border-radius: 5px;
    margin-bottom: 5px;
}
.subtotal__gift{
    display: flex;
    align-items: center;
}
.subtotal__gift input{
    margin-right: 5px;
}
@media  (max-width: 600px) {
    .subtotal{
        width: 80px;
        height: 50px;
        padding: 5px;
        margin-right: 5px;
    }
    .subtotal__btn{
    padding:  8px;
    padding-left: 12px;
    padding-right: 12px;
    border-radius: 3px;
    margin-top: 5px; 
    font-size: 8px;
    }
    .subtotal__gift{
        padding: 5px;
    }
}